import React from 'react'

const Lede = ({ setting }) => {
  return(
    <p className={`text-lg mb-8 ${ setting.active ? 'text-gray-700' : 'text-gray-400' }`}>
      The simulation is currently
      <span className={`font-semibold ${setting.active ? 'text-lime-500' : 'text-red-400' }`}>
        { 
          setting.active 
          ? ` running at a speed of ${setting.speed}` 
          : " not running" 
        }
      </span>.
    </p>
  )
}

export default Lede;