import React, { useState, useEffect } from 'react'
import ChartContainer from './ChartContainer'
import useInterval from 'hooks/useInterval'
import { get } from 'api'
import { ResponsiveScatterPlot } from '@nivo/scatterplot'
import Spinner from 'components/Spinner'
import MultipleCountrySelect from 'components/MultipleCountrySelect'
import { numberWithCommas } from 'utils'

const EarlyEducationAndBelonging = (props) => {
  const [selectedCountries, setSelectedCountries] = useState(['FRA', 'GBR', 'ESP', 'DOM', 'JPN', 'UKR', 'TUR', 'SWE'])

  const [data, setData] = useState({})
  const [status, setStatus] = useState({ loading: true, error: null })
  const [atLeastOneSuccess, setAtLeastOneSuccess] = useState(false)

  const handleSuccess = (data, options) => {
    setData(data)
    if(options.firstTry) setAtLeastOneSuccess(true)
    setStatus({ ...status, loading: false, error: null })
  }

  const handleError = (error) => {
    setStatus({ ...status, loading: false, error: error })
  }

  const fetchData = ({ firstTry }) => {
    if(!firstTry && !atLeastOneSuccess) return

    setStatus({ ...status, loading: true })
    get(`${props.url}?countries=${selectedCountries.join(',')}`, (data) => handleSuccess(data, { firstTry }), handleError)
  }

  useEffect(() => {
    fetchData({ firstTry: true })
  }, [])

  useInterval(() => fetchData({ firstTry: false }), 1000)

  return(
    <ChartContainer
     status={status}
     filter={<MultipleCountrySelect
              selectedCountries={selectedCountries}
              onChange={setSelectedCountries} />}
     {...props}>
     { data.datasets
       ? <ResponsiveScatterPlot
          animate={true}
          data={data.datasets}
          indexBy="country"
          nodeSize={node => node.data.submissions / 10}
          axisBottom={{
            tickSize: 0,
            legend: "Average Years in Early Education",
            legendOffset: 35
          }}
          axisLeft={{
            tickSize: 0,
            legend: "Standardised Sense of Belonging",
            legendOffset: -35
          }}
          margin={{
            top: 30,
            right: 50,
            bottom: 50,
            left: 50
          }}
          tooltip={({ node }) => (
                     <div className="bg-gray-900 text-white rounded p-2">
                      <p className="text-sm text-gray-100 font-semibold">
                        { node.serieId }
                      </p>
                      <p className="text-xs text-gray-100 font-semibold">
                        Submissions: { numberWithCommas(node.data.submissions) }
                      </p>
                      <p className="text-xs text-white">
                        Early education years: <span>{ node.data.x }</span>
                      </p>
                      <p className="text-xs text-white">
                        Sense of belonging: <span>{ node.data.y }</span>
                      </p>
                    </div>
                    )} />
        : <Spinner /> }
    </ChartContainer>
  )
}

export default EarlyEducationAndBelonging