import React, { useState, useEffect, useContext } from 'react'
import { Transition } from '@headlessui/react'
import LoadingChart from './LoadingChart'
import Submissions from './Submissions'
import SubmissionsOverTime from './SubmissionsOverTime'
import LearningHoursPerWeek from './LearningHoursPerWeek'
import EarlyEducationAndBelonging from './EarlyEducationAndBelonging'
import Escs from './Escs'
import ChartForm from './ChartForm'

const Chart = (props) => {
  const [isShowing, setIsShowing] = useState(false)
  const [showChartForm, setShowChartForm] = useState(!props.url)

  useEffect(() => {
    setIsShowing(true)
  }, [])

  const CHARTS_FOR_TYPES = {
    submissions: Submissions,
    submissions_over_time: SubmissionsOverTime,
    learning_hours_per_week: LearningHoursPerWeek,
    early_education_and_belonging: EarlyEducationAndBelonging,
    escs: Escs
  }

  const Chart = CHARTS_FOR_TYPES[props.type]

  const WIDTH_TO_COLUMNS = {
    1: `col-span-1`,
    2: `col-span-2`,
    3: `col-span-3`,
    4: `col-span-4`,
    5: `col-span-5`,
    6: `col-span-6`,
  }

  const HEIGHT_TO_ROWS = {
    1: `row-span-1`,
    2: `row-span-2`,
    3: `row-span-3`,
    4: `row-span-4`
  }

  const columns = WIDTH_TO_COLUMNS[props.width]
  const rows = HEIGHT_TO_ROWS[props.height]

  return(
    <Transition
      show={isShowing}
      className={`${ columns } ${ rows } relative`}
      enter={`transition-opacity duration-${props.entryDuration}`}
      enterFrom="opacity-0"
      enterTo="opacity-100">
      <div
       className="bg-white h-full shadow rounded-lg">
        <div className="px-4 py-5 sm:p-6 h-full">
          { props.url
            ? <Chart { ...props } showChartForm={() => setShowChartForm(true)} />
            : <LoadingChart /> }

          { showChartForm
            && <ChartForm
                showCloseButton={!!props.url}
                setShowChartForm={setShowChartForm}
                { ...props } /> }
        </div>
      </div>
    </Transition>
  )
}

Chart.defaultProps = {
  entryDuration: 300,
  exitDuration: 300
}

export default Chart