import React, { useState, useEffect, useContext } from 'react'
import Loading from './Loading'
import { get } from 'api'
import AppContext from 'contexts/AppContext'
import Chart from './charts/Chart'

const Dashboard = () => {
  const { apiUrl, formToken, currentUser } = useContext(AppContext)

  const [dashboard, setDashboard] = useState(null)
  const [loading, setLoading] = useState(true)
  const [isTransitioning, setIsTransitioning] = useState(true)

  const fetchDashboard = () => {
    setLoading(true)
    get(`${ apiUrl }users/${currentUser.id}/dashboard`, (dashboard) => {
      setDashboard(dashboard)
      setLoading(false)
    })
  }

  useEffect(fetchDashboard, [])

  return(
    <div className="grid grid-cols-6 grid-rows-6 grid-flow-row-dense gap-5 py-4 px-4">
      { loading || isTransitioning
        ? <Loading
           loading={loading}
           { ...{ setIsTransitioning } } />
        : dashboard.charts.map(chart => <Chart
                                          key={chart.id}
                                          onChangeUrl={fetchDashboard}
                                          { ...chart } />) }
    </div>
  )
}

export default Dashboard