import React, { useContext } from 'react'
import AppContext from 'contexts/AppContext'
import { post } from 'api'

const SimulationSettings = ({ setting, setSetting }) => {
  const { formToken, apiUrl } = useContext(AppContext)

  const onStart = (e) => {
    e.preventDefault()

    post(`${apiUrl}/dispatch/start`, { formToken }, setSetting)
  }

  const onStop = (e) => {
    e.preventDefault()

    post(`${apiUrl}/dispatch/stop`, { formToken }, setSetting)
  }

  const onConfigure = (e) => {
    e.preventDefault()

    const body = {
      setting: {
        speed: e.target.value
      }
    }

    post(`${apiUrl}/dispatch/configure`, { body, formToken }, setSetting)
  }

  return(
    <div className="bg-white shadow overflow-hidden sm:rounded-lg">
      <div className="px-4 py-5 sm:px-6">
        <h3 className="font-medium text-gray-800">Simulation Settings</h3>
      </div>
      <div className="border-t border-gray-200">
        <dl>
          <div className="bg-gray-50 px-4 py-5">
            <div className="columns-2">
              <form onSubmit={onStart}>
                <input disabled={ setting.active }
                 type="submit"
                 value="Start"
                 className={ `${ setting.active ? 'bg-gray-300' : 'bg-lime-500 hover:bg-lime-700' } px-5 py-2 text-sm leading-5 rounded-full font-semibold text-white cursor-pointer` } />
              </form>

              <form onSubmit={onStop}>
                <input disabled={ !setting.active }
                 type="submit"
                 value="Stop"
                 className={`${setting.active ? 'bg-red-500 hover:bg-red-700' : 'bg-gray-300'} px-5 py-2 text-sm leading-5 rounded-full font-semibold text-white cursor-pointer`} />
              </form>
            </div>
          </div>
          <div className="bg-white px-4 py-5">
            <label htmlFor="speed" className="block mb-2 text-sm font-medium text-gray-600">Speed</label>
            <input
             type="range"
             name="setting[speed]"
             min="1"
             max="10"
             defaultValue={ `${setting.speed}` }
             className="w-full bg-gray-200 rounded-lg appearance-none cursor-pointer"
             onMouseUp={onConfigure} />
          </div>
        </dl>
      </div>
    </div>
  )
}

export default SimulationSettings
