import React from 'react'

const LoadingLine = () => {
  const POSSIBLE_WIDTHS = ['w-3/4', 'w-2/3', 'w-1/2', 'w-full']

  const width = POSSIBLE_WIDTHS[Math.floor(Math.random() * POSSIBLE_WIDTHS.length)]

  return(
    <div className={`animate-pulse ${ width } h-3 bg-slate-200`} />
  )
}

const LoadingChart = () => {
  const numberOfLines = Math.floor(Math.random() * 3) + 1

  return(
    <>
      <div className="animate-pulse w-full h-48 bg-slate-200" />
      <div className="pt-3 space-y-4">
        <div className="space-y-2">
          {
            [...Array(numberOfLines)]
              .map((_, index) => <LoadingLine
                                  key={index} />)
          }
        </div>
      </div>
    </>
  )
}

export default LoadingChart