import React, { useState, useEffect } from 'react'
import ChartContainer from './ChartContainer'
import useInterval from 'hooks/useInterval'
import { get } from 'api'
import { numberWithCommas } from 'utils'

const Submissions = (props) => {
  const [data, setData] = useState({})
  const [status, setStatus] = useState({ loading: true, error: null })
  const [atLeastOneSuccess, setAtLeastOneSuccess] = useState(false)

  const handleSuccess = (data, options) => {
    setData(data)
    if(options.firstTry) setAtLeastOneSuccess(true)
    setStatus({ ...status, loading: false, error: null })
  }

  const handleError = (error) => {
    setStatus({ ...status, loading: false, error: error })
  }

  const fetchData = ({ firstTry }) => {
    if(!firstTry && !atLeastOneSuccess) return

    setStatus({ ...status, loading: true })
    get(props.url, (data) => handleSuccess(data, { firstTry }), handleError)
  }

  useEffect(() => {
    fetchData({ firstTry: true })
  }, [])

  useInterval(() => fetchData({ firstTry: false }), 1000)

  return(
    <ChartContainer
     status={status}
     {...props}>
      <div className="mt-1 text-5xl font-semibold text-gray-900">
        { data.count
          ? numberWithCommas(data.count)
          : "?" }
      </div>
    </ChartContainer>
  )
}

export default Submissions