import React, { useState, useEffect, useContext } from 'react'
import { useChannel } from '@aersoftware/react-use-action-cable'
import AppContext from 'contexts/AppContext'
import { get, destroy } from 'api'
import Spinner from './Spinner'
import ConfirmDeleteRows from './ConfirmDeleteRows'
import CountryDropdown from './CountryDropdown'

const Country = (props) => {
  const { apiUrl, formToken, actionCable } = useContext(AppContext)
  const { subscribe, unsubscribe, send } = useChannel(actionCable)
  const [isAwaitingConfirm, setIsAwaitingConfirm] = useState(false)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    subscribe({
      channel: 'DispatchesChannel',
      country: props.db_name
    }, {
      received: handleReceiveRow,
      initialized: (e) => {console.log("initialized websocket")},
      connected: (e) => {console.log("connected to websocket")},
      disconnected: (e) => {console.log("disconnected from websocket")}
    })
    return () => {
      unsubscribe()
    }
  }, [])

  const [rowCount, setRowCount] = useState(null)

  const fetchRowCount = () => {
    get(`${apiUrl}countries/${props.db_name}`, ({count}) => {
      setRowCount(count)
      setLoading(false)
    })
  }

  useEffect(fetchRowCount, [])

  const [isReceivingRow, setIsReceivingRow] = useState(false)

  const handleReceiveRow = (dispatch) => {
    const country = dispatch?.country

    if(!country) return

    setIsReceivingRow(true)
    fetchRowCount()
    setTimeout(() => setIsReceivingRow(false), 1000)
  }

  const background = isAwaitingConfirm
                     ? 'red-100'
                     : props.index % 2 == 0
                       ? 'gray-50'
                       : 'white'

  const handleCloseConfirm = () => {
    setIsAwaitingConfirm(false)
    setLoading(true)
    fetchRowCount()
  }
  
  return(
    <dl className="Country">
      <div className={`bg-${background} ${ isReceivingRow ? 'animate-pulse' : '' } px-4 py-5`}>
        <div>
          <div className="CountryHeader flex justify-between h-4">
            <div className="flex">
              <div className="w-3 mr-2 relative top-0.5">
                <span className={`absolute h-3 w-3 ${ isReceivingRow ? 'animate-ping' : '' } inline-flex rounded-full bg-green-400 opacity-75`} />
                <span className="absolute h-3 w-3 inline-flex rounded-full bg-green-400" />
              </div>

              <p className="font-semibold text-xs text-gray-400 mr-2">
                { props.db_name.toUpperCase() }
              </p>
              <p className="text-xs text-gray-500 relative">
                { loading
                  ? <Spinner className="w-4 h-4 inline" />
                  : rowCount }</p>

              {/*<span className="text-xs text-gray-600">{props.address}</span>*/}
            </div>

            <CountryDropdown
             {...props}
             onEmptyDatabase={() => setIsAwaitingConfirm(true)} />
          </div>
          
        </div>
      </div>

      <ConfirmDeleteRows country={props} open={isAwaitingConfirm} onClose={handleCloseConfirm} />
    </dl>
  )
}

export default Country