import React, { useRef, useState, useLayoutEffect, useContext } from 'react'
import Spinner from 'components/Spinner'
import CloseButton from 'components/CloseButton'
import AppContext from 'contexts/AppContext'
import { put } from 'api'

const ChartForm = (props) => {
  const { apiUrl, formToken } = useContext(AppContext)

  const [style, setStyle] = useState({})
  const container = useRef(null)
  const urlInput = useRef(null)

  useLayoutEffect(() => {
    const containerHeight = container.current.getBoundingClientRect().height
    setStyle({ marginTop: -1/2 * containerHeight })
  }, [])

  const [url, setUrl] = useState(props.url)
  const [isSubmitting, setIsSubmitting] = useState(false)

  const handleSubmit = (e) => {
    e.preventDefault()
    setIsSubmitting(true)

    const body = {
      chart: { url }
    }

    put(`${apiUrl}charts/${props.id}`, { formToken, body }, () => {
      setIsSubmitting(false)
      props.onChangeUrl()
    })
  }

  const isEmpty = !(url && url.length > 0)

  return (
    <div
     ref={container}
     className="absolute left-0 right-0 top-1/2 bg-white shadow sm:rounded-lg z-10"
     style={style}>
      <div className="px-4 py-5 sm:p-6">
        <div className="flex justify-between items-center">
          <p className="text-sm font-semibold text-indigo-600 uppercase tracking-wide">Chart</p>
          { 
            props.showCloseButton
            && <CloseButton onClick={() => props.setShowChartForm(false)} />
          }
        </div>
        <h3 className="text-lg leading-6 font-medium text-gray-900 mb-2">{props.title}</h3>
        <div className="max-w-xl text-sm text-gray-500 mb-5">
          <p>
            { props.description }
          </p>

          <a
           href={props.specification}
           target="_blank"
           className="text-xs text-blue-400"> Click here for the full component specification.</a>
        </div>
        <p className="text-xs font-medium text-gray-500 mb-2">
          Polling URL <span className="font-normal text-gray-400">(will be polled once per second)</span>
        </p>
        <form onSubmit={handleSubmit} className="sm:flex sm:items-center">
          <div className="w-full sm:max-w-xs">
            <label htmlFor="url" className="sr-only">
              URL
            </label>
            <input
              onChange={(e) => setUrl(e.target.value)}
              defaultValue={url}
              required
              type="url"
              name="url"
              id="url"
              className="shadow-sm focus:ring-indigo-500 focus:border-indigo-500 block w-full sm:text-sm border-gray-300 rounded-md"
              placeholder="e.g. https://your-api.com"
            />
          </div>
          <button
            type="submit"
            disabled={isSubmitting}
            className={`mt-3 w-full inline-flex items-center justify-center px-4 py-2 border border-transparent shadow-sm font-medium rounded-md text-white ${ isSubmitting || isEmpty ? 'bg-indigo-300' : 'bg-indigo-600 hover:bg-indigo-700 focus:bg-indigo-800' } focus:outline-none sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm`}>
            { isSubmitting && <Spinner className="h-5 w-5" /> }
            {
              isSubmitting
                ? <span>Submitting</span>
                : props.url
                  ? <span>Update</span>
                  : <span>Save</span>
            }
          </button>
        </form>
      </div>
    </div>
  )
}

export default ChartForm
