import React, { useState, useEffect, useRef, useLayoutEffect } from 'react'
import useInterval from 'hooks/useInterval'
import { get } from 'api'

import ChartDropdown from './ChartDropdown'
import Spinner from 'components/Spinner'

import { numberWithCommas } from 'utils'

const ChartContainer = (props) => {
  const container = useRef(null)
  const meta = useRef(null)

  const [chartStyle, setChartStyle] = useState({})

  useLayoutEffect(() => {
    const containerHeight = container.current.getBoundingClientRect().height
    const metaHeight = meta.current.getBoundingClientRect().height

    setChartStyle({
      height: containerHeight - metaHeight,
      width: "100%"
    })
  }, [])

  return(
    <div className={`h-full ${props.className}`} ref={container}>
      { props.status.error 
        && <div className="flex items-center absolute top-0 left-0 right-0 w-full bg-red-500 rounded-t-lg py-2 pl-6">
            <svg
             xmlns="http://www.w3.org/2000/svg"
             className="h-6 w-6 text-white mr-1"
             fill="none"
             viewBox="0 0 24 24"
             stroke="currentColor"
             strokeWidth={2}>
              <path strokeLinecap="round" strokeLinejoin="round" d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
            </svg>
            <p className="text-sm text-white font-semibold">{ props.status.error.message }</p>
           </div> }
      <div className={`${props.status.error ? 'pt-8' : ''} h-full`}>
        <div className="flex justify-between items-center" ref={meta}>
          <div>
            <p className="text-sm font-semibold text-indigo-600 uppercase tracking-wide">
              { props.title }
            </p>
            {
              props.subtitle
              && <p className="text-xs font-semibold text-gray-300 uppercase tracking-wide">
                  { props.subtitle }
                 </p>
            }
          </div>

          <div className="flex items-center">
            <div className="mr-3">
              { props.filter }
            </div>
            <ChartDropdown {...props} />
          </div>
        </div>

        <div className="pt-2" style={chartStyle}>
          { props.children }
        </div>
      </div>
    </div>
  )
}

ChartContainer.defaultProps = {
  className: '',
  filter: null
}

export default ChartContainer